@import "../mixins.scss";

html, body {
    margin: 0;
}

.container-signin {
    height: 100vh; 
    width: 100%;
    display: flex;
  
    justify-content: center;
    margin-top: 2%;

    .wrapper-signin {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
     

        .signin-form {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 23%;
            padding: 1.5rem;
            border-radius: 20px;
            background-color: white;
            box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.35);
            
            .signin-title {
                @include primary-color;
                @include font-primary(1.8rem, 600);
                margin-bottom: 1.2rem;
            }

            .input-group {
                display: flex;
                flex-direction: column;
                width: 100%;
                margin-bottom: 1rem;

                .input-label {
                    color: #333;
                    font-weight: 600;
                    font-size: 0.9rem;
                    margin-bottom: 0.3rem;
                    margin-top: 1rem;
                }

                .signin-input {
                    border: 1px solid #ddd;
                    padding: 0.6rem; 
                    border-radius: 10px;
                    width: 100%;
                    box-sizing: border-box;
                    height: 42px;
                    @include font-primary(1rem, 500);
                    transition: border-color 0.3s ease;

                    &:focus {
                        outline: none;
                        border-color: #6EA0F9;
                        box-shadow: 0 0 5px rgba(110, 160, 249, 0.3);
                        transform: scale(1.05);
                        transition: transform 0.3s ease;
                    }
                }
            }

            .check-input {
                display: flex;
                justify-content: center;
                gap: 5px;
                width: 100%;
                @include font-primary(1rem, 400);
                margin-top: 4%;
            }

            .signin-button {
                margin-top: 1.2rem;
                width: 75%;
                padding: 0.8rem;
                border-radius: 25px;
                background-color: #f2a602;
                cursor: pointer;
                transition: background-color 0.3s ease, transform 0.2s ease;
                box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);

                &:hover {
                    background-color: #d69202;
                    transform: translateY(-2px);
                }
            }
            
         }
    }
}
